const Header = () => {
  const mainHeaderNav = document.getElementById("main-header-nav");
  const mobileNavButton = document.getElementById("mobile-nav-button");
  const itemSearchInput = document.getElementById("desktop-search-input");
  const hiddenNavLists = document.getElementsByClassName("header__hidden-nav");
  const dropdownListButtons = document.getElementsByClassName(
    "header__dropdown-btn"
  );
  const documentBody = document.body;
  const mainNavClasslist = mainHeaderNav.classList;
  const navButtonClasses = mobileNavButton.classList;

  document.addEventListener("click", (e) => handleSearchBarClick(e));

  for (let i = 0; i < dropdownListButtons.length; i++) {
    const dropdownListButton = dropdownListButtons[i];
    dropdownListButton.addEventListener("click", (e) =>
      handleDropdownButtonClick(e, i)
    );
  }

  mobileNavButton.addEventListener("click", (e) =>
    handleMobileNavButtonClick()
  );

  window.addEventListener("resize", (e) => handleWindowResize());

  let matchMedia = window.matchMedia("(min-width: 992px)");

  /**
   * Removes mobile navigation styles on window resize
   */
  function handleWindowResize() {
    if (matchMedia.matches) {
      documentBody.classList.remove("hide-overflow");
      mainNavClasslist.remove("active");
      navButtonClasses.remove("active");
    }
  }

  /**
   * Handles search bar button click
   */
  function handleSearchBarClick(e) {
    const inputContainer = itemSearchInput.parentElement;
    if (inputContainer.contains(e.target)) {
      inputContainer.classList.add("active");
    } else {
      inputContainer.classList.remove("active");
    }
  }

  /**
   * Opens and closes corresponding dropdown nav on dropdown button click
   * @param {number} buttonNum - index number of button clicked
   */
  function handleDropdownButtonClick(e, buttonNum) {
    e.preventDefault;
    // console.log(
    //   "THINGS",
    //   buttonNum,
    //   dropdownListButtons,
    //   dropdownListButtons[buttonNum]
    // );
    const buttonClicked = dropdownListButtons[buttonNum];
    const buttonClasses = buttonClicked.classList;

    const navToOpen = hiddenNavLists[buttonNum];
    const navClasslist = navToOpen.classList;

    if (matchMedia.matches) {
      if (!buttonClasses.contains("header__dropdown-btn-with-border")) {
        for (let i = 0; i < dropdownListButtons.length; i++) {
          const dropdownButton = dropdownListButtons[i];

          const hiddenNavList = hiddenNavLists[i];

          if (i !== buttonNum) {
            dropdownButton.classList.remove("active");
            hiddenNavList.classList.remove("active");
          }
        }
      }
    }

    buttonClasses.toggle("active");
    navClasslist.toggle("active");
  }

  /**
   * Handles mobile navigation button click
   */
  function handleMobileNavButtonClick() {
    documentBody.classList.toggle("hide-overflow");
    mainNavClasslist.toggle("active");
    navButtonClasses.toggle("active");
  }
};

export default Header;
