import Glide, {
  Breakpoints,
  Controls,
  Swipe,
} from "@glidejs/glide/dist/glide.modular.esm";

const InstagramSlider = () => {
  const instagramGlideElement = document.getElementsByClassName(
    "instagram-glide"
  );

  if (instagramGlideElement.length > 0) {
    let instagramGlide = new Glide(".instagram-glide", {
      type: "carousel",
      startAt: 0,
      perView: 4,
      gap: 20,
      breakpoints: {
        768: {
          perView: 1,
          peek: {
            before: 50,
            after: 50,
          },
        },
        992: { perView: 2 },
        1140: { perView: 3, autoplay: 3000 },
      },
    });

    // TODO: See if this is a valid test to check if elements exist
    instagramGlide.mount({ Controls, Breakpoints, Swipe });
  }
};

export default InstagramSlider;
