import Glide, {
  Controls,
  Breakpoints,
} from "@glidejs/glide/dist/glide.modular.esm";

const isLoaded = (image) => {
  return image.complete && image.naturalHeight !== 0;
};

const HomepageSlider = () => {
  let container = document.getElementsByClassName("homepage-slider");

  if (container.length > 0) {
    container = container[0];

    let imageContainer = container.getElementsByClassName("image-container")[0];

    let count = document.querySelectorAll(
      ".homepage-slider .glide__slide:not(.glide__slide--clone)"
    ).length;

    let loadNext = new Image();

    loadNext.onload = () => {
      container.classList.remove("loading");
    };

    let homepageGlide = new Glide(".homepage-slider", {
      type: "carousel",
      startAt: 0,
      gap: 0,
      perView: 1,
      animationDuration: 600,
    });

    homepageGlide.on(["run", "mount.after"], () => {
      container.classList.add("transition");

      let activeSlide = document.getElementsByClassName(
        `glide__slide-${homepageGlide.index}`
      )[0];

      if (window.matchMedia('(max-width: 767px')) {
        loadNext.src = activeSlide.dataset.mobileImage;
      } else {
        loadNext.src = activeSlide.dataset.image;
      }
      
      if (!isLoaded(loadNext)) {
        container.classList.add("loading");
      }
    });

    homepageGlide.on("move.after", (event) => {
      imageContainer.style.backgroundImage = `url('${loadNext.src}')`;
      container.classList.remove("transition");
      updateSliderBar();
    });

    function updateSliderBar() {
      const sliderIndicators = document.getElementsByClassName(
        "homepage-slider__indicator-button"
      );
      const progressBar = document.getElementsByClassName(
        "homepage-slider__progress-bar"
      )[0];
      const currentSliderNum = homepageGlide.index;
      const totalSlides = sliderIndicators.length;

      let progressWidth = ((currentSliderNum + 1) / totalSlides) * 100;

      progressBar.style.width = progressWidth + "%";
    }

    homepageGlide.mount({ Controls, Breakpoints });
  }
};

export default HomepageSlider;
