const HatSwatches = () => {
  const hatSwatchGrids = document.getElementsByClassName("hat-swatch__grid");

  if (hatSwatchGrids.length > 0) {
    for (let i = 0; i < hatSwatchGrids.length; i++) {
      const hatSwatchGrid = hatSwatchGrids[i];
      const hatSwatchLists = hatSwatchGrid.querySelectorAll(
        ".hat-swatch__btn-list"
      );

      for (let j = 0; j < hatSwatchLists.length; j++) {
        const hatSwatchList = hatSwatchLists[j];
        const hatSwatches = hatSwatchList.querySelectorAll(
          ".hat-swatch__swatch"
        );

        const totalSwatches = hatSwatches.length;

        if (totalSwatches < 4) {
          hatSwatchList.style.gridTemplateColumns = `repeat(${totalSwatches}, 20%)`;
        } else {
          hatSwatchList.style.gridTemplateColumns = `repeat(4, 20%)`;
        }

        // if there is only one color variant do not add event listener
        if (hatSwatches.length > 1) {
          for (let k = 0; k < hatSwatches.length; k++) {
            const hatSwatch = hatSwatches[k];
            hatSwatch.addEventListener("click", (e) =>
              handleSwatchClick(e, i, j)
            );
          }
        }
      }
    }
  }

  /**
   * Changes the color of product preview on click
   * @param {object} e - event that was handled
   * @param {number} hatSwatchGridIndex - index of hat swatch grid
   * @param {number} hatSwatchIndex - index of hat swatch container
   */
  function handleSwatchClick(e, hatSwatchGridIndex, hatSwatchIndex) {
    const buttonClicked = e.target;
    const newProductImage = buttonClicked.dataset.productImage;

    const hatSwatchGrid = hatSwatchGrids[hatSwatchGridIndex];
    const hatSwatches = hatSwatchGrid.querySelectorAll(".hat-swatch");
    const hatSwatchToChange = hatSwatches[hatSwatchIndex];
    const hatImageElem = hatSwatchToChange.querySelector(
      ".hat-swatch__product-image"
    );

    hatImageElem.src = newProductImage;
  }
};

export default HatSwatches;
