const ProductFilter = () => {
  const productFilterSection = document.getElementById(
    "product-filter-section"
  );

  if (productFilterSection !== null) {
    const mobileProductFilterButton = document.getElementById(
      "product-filter-button"
    );
    const productFilterModal = document.getElementById(
      "product-filter-container"
    );
    const closeProductFilterButton = document.getElementById(
      "close-product-filter"
    );
    const filterDropdownButtons = productFilterSection.querySelectorAll(
      ".dropdown-button"
    );
    const filerLists = productFilterSection.querySelectorAll(
      ".product-filter__hidden-list"
    );
    const filterCheckMarkInputs = productFilterSection.querySelectorAll(
      ".product-filter__checkbox"
    );

    const WINDOW_URL = "https://vintagestateapparel.com/collections/all/";

    let PAGINATION = "";

    let selectedFilters = []; // Selected filter options array

    updateFilterList(); // get selected filter options from url
    applyCheckMarks(selectedFilters); // apply check marks to selected items
    showCurrentFilterItemsSelected(); // gets selected filter options to show under filter dropdown buttons

    // Apply event listeners to various elements
    for (let i = 0; i < filterCheckMarkInputs.length; i++) {
      const checkMarkInput = filterCheckMarkInputs[i];

      checkMarkInput.addEventListener("click", (e) =>
        handleFilterInputClick(e)
      );
    }

    for (let i = 0; i < filterDropdownButtons.length; i++) {
      const filterDropdownButton = filterDropdownButtons[i];
      filterDropdownButton.addEventListener("click", (e) =>
        handleFilterDropdownButtonClick(i)
      );
    }

    mobileProductFilterButton.addEventListener("click", (e) =>
      openProductFilterModal()
    );

    closeProductFilterButton.addEventListener("click", (e) =>
      closeProductFilterModal()
    );

    window.addEventListener("resize", (e) => closeModalOnResize());

    let matchMedia = window.matchMedia("(min-width: 992px)");

    /**
     * Removes mobile filter navigation styles on window resize
     */
    function closeModalOnResize() {
      if (matchMedia.matches) {
        document.body.classList.remove("hide-overflow");
        productFilterModal.classList.remove("active");
      }
    }

    /**
     * Opens mobile filter nav
     */
    function openProductFilterModal() {
      document.body.classList.add("hide-overflow");
      productFilterModal.classList.add("active");
      window.scrollTo(0, 0);
    }

    /**
     * Closes mobile filter nav
     */
    function closeProductFilterModal() {
      document.body.classList.remove("hide-overflow");
      productFilterModal.classList.remove("active");
    }

    /**
     * Updates selected filter items list on window load
     * based off info from the url
     */
    function updateFilterList() {
      let currentURL = window.location.href;
      const urlParams = new URLSearchParams(window.location.search);
      const currentPage = urlParams.get("page");
      let filterProductTagArr = [];

      if (currentURL.includes("collections/all")) {
        let filterProductTags = currentURL.split("/").pop();

        if (filterProductTags !== "all") {
          filterProductTags = filterProductTags.replace(
            `?page=${currentPage}`,
            ""
          );
          filterProductTagArr = filterProductTags.split("+");
        }
      } else if (currentURL.includes("/collections/")) {
        let filterProductTags = currentURL.split("/").pop();

        switch (filterProductTags) {
          case "original":
            filterProductTags = "iconic";
            break;
          case "hats-and-headwear":
            filterProductTags = "headwear";
            break;
          case "bottles":
            filterProductTags = "drinkware";
            break;
        }

        filterProductTagArr = filterProductTags.split("+");
      }

      if (currentPage !== null) {
        PAGINATION = `?page=1`;
      }

      for (let i = 0; i < filterProductTagArr.length; i++) {
        const productFilterTag = filterProductTagArr[i];

        selectedFilters = [...selectedFilters, productFilterTag];
      }
    }

    /**
     * Handles check mark input click.
     * @param {Object} e - event that was handled
     */
    function handleFilterInputClick(e) {
      const inputClicked = e.target;
      const inputValue = inputClicked.value;
      const inputIsChecked = inputClicked.checked;
      const inputFilterType = inputClicked.dataset.filterType;
      const elements = document.querySelectorAll(
        `[data-filter-type='${inputFilterType}']`
      );

      for (let i = 0; i < elements.length; i++) {
        const element = elements[i];
        element.checked = false;
        const index = selectedFilters.indexOf(element.value);
        if (index > -1) {
          selectedFilters.splice(index, 1);
        }
      }

      if (inputIsChecked) {
        inputClicked.checked = true;
        selectedFilters.push(inputValue);
      }

      let joinFilters = selectedFilters.join("+");

      window.location.href = WINDOW_URL + joinFilters + PAGINATION;

      // window.open(WINDOW_URL + joinFilters);

      showCurrentFilterItemsSelected();
    }

    /**
     * Updates small text under filter buttons to show which filter options were selected
     */
    function showCurrentFilterItemsSelected() {
      const currentFilterItemsSelected = document.getElementsByClassName(
        "product-filter__selected-items"
      );

      const productTypesWithoutStyles = ["headwear", "drinkware"];

      let newValue;
      let newValueDataset;

      // console.log(filterDropdownButtons.length);

      for (let i = 0; i < filterCheckMarkInputs.length; i++) {
        const checkMarkInput = filterCheckMarkInputs[i];

        if (checkMarkInput.checked) {
          newValue = checkMarkInput.value;
          newValue = newValue.split("-").join(" ");
          newValueDataset = checkMarkInput.dataset.filterType;
        }

        for (let k = 0; k < productTypesWithoutStyles.length; k++) {
          const productType = productTypesWithoutStyles[k];

          if (productType === newValue) {
            filterDropdownButtons[1].disabled = true;
          }
        }

        for (let j = 0; j < currentFilterItemsSelected.length; j++) {
          const currentFilterItem = currentFilterItemsSelected[j];
          const itemDataSet = currentFilterItem.dataset.filterType;

          if (itemDataSet === newValueDataset) {
            currentFilterItem.innerHTML = newValue;
          }
        }
      }
    }

    /**
     * Applies check marks to selected filter options on load
     * @param {array} selectedFilterOptions - array of strings that represent selected filter options
     */
    function applyCheckMarks(selectedFilterOptions) {
      for (let i = 0; i < filterCheckMarkInputs.length; i++) {
        const checkMarkInput = filterCheckMarkInputs[i];

        for (let j = 0; j < selectedFilterOptions.length; j++) {
          const selectedFilter = selectedFilterOptions[j];

          if (checkMarkInput.value === selectedFilter) {
            checkMarkInput.checked = true;
          }
        }
      }
    }

    /**
     * Opens dropdown menu for filter options
     * only one filter nav can be on large screen sizes
     * @param {number} buttonNum - index of dropdown button clicked
     */
    function handleFilterDropdownButtonClick(buttonNum) {
      const buttonClicked = filterDropdownButtons[buttonNum];
      const buttonClasses = buttonClicked.classList;

      const listToOpen = filerLists[buttonNum];
      const listClasses = listToOpen.classList;

      if (matchMedia.matches) {
        for (let i = 0; i < filterDropdownButtons.length; i++) {
          const filterDropdownButton = filterDropdownButtons[i];
          const filerList = filerLists[i];

          if (i !== buttonNum) {
            filterDropdownButton.classList.remove("active");
            filerList.classList.remove("active");
          }
        }
      }

      // open filter opens for button clicked
      listClasses.toggle("active");
      buttonClasses.toggle("active");
    }
  }
};

export default ProductFilter;
